/*LEGEND*/
.chart-legend {
	display: flex;
	flex-direction: column;
	flex-wrap: wrap;
	height: 125px;
	width: 60%;
}

.legend-item {
	display: flex;
	align-items: center;
	gap: 10px;
	margin: 5px;
}

.legend-color {
	width: 15px;
	height: 30px;
}

.legend-text {
	font-size: 14.5px;
	font-weight: 550;
}

.legend-color#excellent {
	background-color: #00c602;
}
.legend-color#minorimprovementsavailable {
	background-color: #fff700;
}
.legend-color#improvementsavailable {
	background-color: #ffd200;
}
.legend-color#severalimprovementsrecommended {
	background-color: #ff8800;
}
.legend-color#addressconcernssoon {
	background-color: #de3b04;
}