/**/
.left-align {
	text-align: left;
}

.right-align {
	text-align: right;
}

.double-form-item {
	font-size: 1.25rem;
	display: grid;
	grid-template-columns: 85% 10%;
	gap: 5%;
	padding-top: 3.5px;
	padding-bottom: 3.5px;
	align-items: center;
	justify-content: center;
}
.double-form-item>.form-info-item {
	gap: 5%;
}

.paragraph-cont {
	width: 100%;
	text-align: left;
	font-size: 1.15rem;
}

.paragraph-header {
	font-weight: 600;
	font-size: 1.5rem;
	color: #212121;
}

.mobile-only {
	display: none;
}

#membership-options-card>.card-menubar {
	position: relative;
	font-size: 15px
}
@media screen and (max-width: 450px) {
	.mobile-only {
		display: unset;
	}
}

input.form-item-empty {
	border-bottom: 2.5px solid rgb(157, 45, 45);
}

.double-form-item>:first-child>input {
	padding-top: 10px;
	padding-bottom: 10px;
}
.double-form-item>:first-child>.form-info-title {
	font-size: 20px;
}

/*CUSTOM FORM STYLING*/
canvas {
	height: 100px !important;
	width: 520px !important;
}
#vogel-header-logo {
	height: 200px;
	width: 400px;
	align-self: center;
}
/*Custom styling for monthly plan field*/
#monthly-plan-field {
	display: grid;
	grid-template-columns: 45% 12.5%;
	gap: 0;
	padding: 0;
	justify-content: end;
	padding-right: 7%;
}
#monthly-plan-field>:first-child>.form-info-title {
	min-width: 90%;
	max-width: 90%;
	height: 100%;
	margin-right: 37.5%;
	text-align: right;
}
#monthly-plan-field>:first-child>:nth-child(2) {
	display: none;
}
#monthly-plan-field>:nth-child(2) {
	display: grid;
	grid-template-columns: 70% 30%;
	align-items: center;
	justify-content: center;
}
#monthly-plan-field>:nth-child(2)>:first-child {
	order: 1;
}

#membership-level-select-cont {
	display: grid;
	width: 95%;
	margin-left: 2.5%;
	grid-template-columns: 50% 50%;
	align-items: center;
	justify-content: end;
}
#membership-level-select-cont>.form-info-title {
	font-size: 1.25rem;
	width: 90%;
	text-align: right;
}
.plan-header {
	margin-right: 20px;
}
.plan-header>.header-text {
	font-size: 1.23rem;
	text-align: right;
	font-weight: 900;
	color: #212121;
}

/*Membership total css*/
#membership-options-card-card-content>.card-content {
	padding: 5px;
}
#membership-options-total-cont {
	display: grid;
	grid-template-columns: 85% 10%;
}
#membership-options-total-cont>:nth-child(2) {
	width: 100%;
}
#membership-total-price-cont {
	display: flex;
	width: 15%;
	gap: 25%;
	padding-right: 5%;
}
#membership-total-price-cont>.form-info-title {
	font-size: 1.4rem;
	text-align: right;
}

#membership-total-price-cont>:nth-child(2) {
	font-size: 1.4rem;
	border-bottom: 1px solid black;
	text-align: center;
}
#membership-total-price-cont>:nth-child(2)::before {
	content: "$";
}

#membership-options-total-cont {
	padding-top: 0;
}

#terms-and-conditions-content {
	min-height: 50vh;
	max-height: 50vh;
	overflow-y: overlay !important;
}

#terms-checkbox-cont {
	align-items: center;
}

#terms-checkbox-mobile-cont {
	display: none;
}

#sig-box {
	height: 100px;
	width: 544px;
	background-color: #d3d3d3;
	border-radius: 10px;
	touch-action: none;
}

#termsReadNotif {
	font-size: 1.25rem;
	min-height: fit-content;
	min-width: fit-content;
	align-self: center;
	justify-self: center;
}

@media screen and (max-width: 1300px) {
	.float-container.overlay {
		background-color: #fff;
	}
	/*Hide the outer container*/
	#contract-card>.card-menubar {
		display: none;
	}
	#contract-card {
		filter: unset;
		width: 100%;
		overflow-y: overlay;
	}

	/*Set the outer containers to use a flex column layout*/
	#top-cards {
		display: flex;
		flex-direction: column-reverse !important;
		height: fit-content;
		overflow-x: hidden !important;
		gap: 20px;
	}

	/*Change the width/height of each card*/
	#membership-options-card {
		min-width: 100%;
		max-width: 100%;
	}
	#terms-card {
		min-width: 100%;
		max-width: 100%;
	}
	#customer-auth-card {
		min-height: fit-content;
		max-height: fit-content;
	}
	#customer-auth-card-card-content {
		display: flex;
		flex-direction: column;
	}

	/*Adjust the styling of the auth card grid*/
	#auth-form-cont {
		display: flex;
		flex-direction: column;
	}
}


@media screen and (max-width: 450px) {
	/*Set the outer containers to use a flex column layout*/
	#top-cards {
		display: flex;
		flex-direction: column-reverse !important;
		height: fit-content;
		overflow-x: hidden !important;
	}
	#contract-card-card-content {
		display: flex;
		flex-direction: column;
	}
	#customer-auth-card-card-content {
		display: flex;
		flex-direction: column !important;
	}
	/*Modify height of card contents used as outer containers*/
	#auth-card-content {
		height: fit-content;
		display: flex;
		flex-direction: column;
	}
	#contract-card {
		overflow-y: overlay;
		max-height: 92.5vh !important;
	}
	#terms-and-conditions-content {
		min-height: fit-content !important;
		max-height: fit-content;
	}
	/*Hide the Menubars and outer cards*/
	#contract-card>.card-menubar,
	#terms-card>.card-menubar,
	#membership-options-card>.card-menubar,
	#customer-auth-card>.card-menubar {
		display: none;
	}
	#contract-card,
	#terms-card,
	#membership-options-card,
	#customer-auth-card {
		filter: unset;
		min-width: 100%;
		max-width: 100%;
		min-height: fit-content !important;
		max-height: fit-content !important;
	}

	/*Strip the padding back*/
	#contract-card,#contract-card>.card-content {
		padding: 0;
		margin: 0;
	}
	#terms-card, #customer-auth-card, #membership-options-card-card-content {
		padding: 0;
		margin: 0;
	}
	.plan-form {
		padding: 0;
		margin: 0;
		min-width: 100%!important;
		max-width: 100%!important;
	}

	/*Set the customer form to flex and resize its inputs*/
	#auth-form-cont {
		display: flex;
		flex-direction: column;
		padding: 0;
		margin: 0;
	}

	/*Modify the font sizes to scale to mobile better*/
	.double-form-item {
		font-size: 1.05rem;
	}
	#membership-level-select-cont>.form-info-title {
		font-size: 1.05rem;
		align-self: center;
		justify-self: center;
	}
	
	/*Adjust the grid of the double-form-items*/
	.double-form-item>:first-child>.form-info-title {
		text-align: right;
		align-self: center;
		justify-self: center;
	}
	.double-form-item>:first-child>input {
		padding-top: 15px;
		padding-bottom: 15px;
	}

	/*Adjust the height of the canvas*/
	canvas {
		height: 100px !important;
		width: 350px !important;
	}

	/*Hide the form terms card*/
	#terms-card {
		display: none;
	}

	/*Modify the styling of the logo/header*/
	.print-logos {
		display: flex;
		flex-direction: column;
		width: 100%;
	}

	/*Display the terms checkbox*/
	#terms-checkbox-mobile-cont {
		display: flex;
		margin-bottom: 10px;
		margin-top: 5px;
	}
}

@media print {
	body {
		background-color: #fff;
	}
	.mobile-only {
		display: unset;
	}
	.float-container.overlay {
		overflow: visible;
		position: relative;
		background-color: #fff;
	}
	/*Resize header logo*/
	#vogel-header-logo {
		height: 100px;
		width: 250px;
		align-self: center;
	}
	#rewards-header {
		text-align: right;
		margin-left: 60px;
	}
	.print-logos {
		display: flex;
		flex-direction: row;
		width: 100%;
	}

	/*Hide the membership level header and terms card*/
	#membership-options-card-card-content>:first-child>.header,
	#terms-card {
		display: none;
	}

	#terms-card-print {
		position: relative;
		display: flex;
		page-break-before: always;
	}

	/*Set the outer containers to use a flex column layout*/
	#top-cards {
		display: flex;
		flex-direction: column-reverse !important;
		height: fit-content;
		overflow-x: hidden !important;
	}
	#contract-card-card-content {
		display: flex;
		flex-direction: column;
	}
	#customer-auth-card-card-content {
		display: flex;
		flex-direction: column !important;
		padding: 0;
		margin: 0;
	}
	/*Modify height of card contents used as outer containers*/
	#auth-card-content {
		height: fit-content;
		display: flex;
		flex-direction: column;
	}
	#contract-card {
		overflow: auto !important;
		min-height:max-content;
		max-height: max-content;
		border-radius: 0px;
	}
	#terms-and-conditions-content {
		min-height: fit-content !important;
		max-height: fit-content;
	}
	/*Hide the Menubars and outer cards*/
	#contract-card>.card-menubar,
	#terms-card>.card-menubar,
	#membership-options-card>.card-menubar,
	#customer-auth-card>.card-menubar {
		display: none;
	}
	#contract-card,
	#terms-card,
	#membership-options-card,
	#customer-auth-card {
		filter: unset;
		min-width: 100%;
		max-width: 100%;
		min-height: fit-content !important;
		max-height: fit-content !important;
	}

	/*Strip the padding back*/
	#contract-card,#contract-card>.card-content {
		padding: 0;
		margin: 0;
	}
	#terms-card, #customer-auth-card, #membership-options-card-card-content, #terms-and-conditions-content {
		padding: 0;
		margin: 0;
	}
	.plan-form {
		padding: 0;
		margin: 0;
		min-width: 100%!important;
		max-width: 100%!important;
	}

	/*Set the customer form to flex and adjust its form item styling*/
	#auth-form-cont {
		display: grid;
		flex-direction: column;
		padding: 0;
		margin: 0;
		grid-template-columns: repeat(2, 50%);
	}
	#auth-form-cont>.form-info-item {
		grid-template-columns: 20% 80%;
		gap: 0;
	}
	#auth-form-cont>.form-info-item>.form-info-title {
		font-size: 16px !important;
		text-align: left;
		width: 100%;
		margin: 0;
		padding: 0;
	}
	#auth-form-cont>.form-info-item>input {
		width: 100%;
	}

	/*Modify the font sizes to scale to mobile better*/
	.double-form-item {
		font-size: 1.025rem;
	}
	#membership-level-select-cont>.form-info-title {
		font-size: 1.025rem;
		align-self: center;
		justify-self: center;
		color: rgb(255,255,255);
	}
	#membership-level-select-cont>.form-info-title::after {
		content: 'Selected Plan:';
		color:#212121;
	}
	.paragraph-cont {
		font-size: 16px;
	}
	
	/*Adjust the grid of the double-form-items*/
	.double-form-item>:first-child {
		display: grid;
		grid-template-columns: 60% 15%;
	}
	.double-form-item>:first-child>.form-info-title {
		text-align: right;
		align-self: center;
		justify-self: center;
		width: 100%;
	}
	.double-form-item>:first-child>input {
		padding-top: 10px;
		padding-bottom: 10px;
	}

	/*Adjust the height of the canvas*/
	canvas {
		height: 100px;
	}

	/*Modify the styling of inputs to be print friendly*/
	input {
		border: 0px;
		border-bottom: 2px solid black;
		border-radius: 0px;
		background-color: transparent;
		width: 100%;
	}

	/*Hide the text buttons*/
	.action-button {
		display: none;
	}

	/*Modify the grid of the plan options/enhancement sections*/
	.plan-form {
		display: grid;
		grid-template-columns: repeat(2, 50%);
	}

	/*Adjust the styling of the dropdown*/
	.dropdown-arrow {
		display: none;
	}
	.dropdown-option.selected {
		outline: 0;
		border-bottom: 2px solid black;
		border-radius: 0px;
	}
}


/*SCROLLBAR WEBKIT*/
*::-webkit-scrollbar {
    display: block;
    width: 8px;
}
*::-webkit-scrollbar-button {
    display: none;
}
*::-webkit-scrollbar-track {
    background-color: #00000000;
}
*::-webkit-scrollbar-track-piece {
    background-color: #00000000;
}
*::-webkit-scrollbar-thumb {
    background-color: #000000c7;
    border: 1px solid #00000040;
    border-radius: 12px;
}