.drop-note {
	position: absolute;
	min-width: 150px;
	min-height: 100px;
	right: 10px;
	top: 10px;
	display: flex;
	flex-direction: column;
	border-radius: 10px;
	overflow: hidden;
}
.drop-note-close {
	text-align: right;
	padding-right: 10px;
	padding-left: 10px;
	font-weight: 600;
	cursor: pointer;
}
.drop-note#yellow {
	background-color: rgb(241, 241, 139);
	border: 1px solid rgb(192, 192, 132);
}
.drop-note#yellow>.drop-note-close {
	background-color: rgb(192, 192, 132);
}
.drop-note#yellow>.drop-note-close:hover {
	background-color: rgb(156, 156, 109);
}

