/*BARS*/
.bar-chart-container {
	position: relative;
	border: 1px solid #ccc;
	border-radius: 10px;
	overflow: hidden;
}
.bar-chart-bar-outer {
	width: 100%;
	text-align: left;
	font-size: 30px;
}
.bar-chart-bar {
	position: relative;
	border-top-right-radius: 5px;
	border-bottom-right-radius: 5px;
	animation: fadein 1s;
	height: 60px;
	z-index: 50;
	margin-top: 10px;
	margin-bottom: 10px;
}
.bar-chart-bar.excellent {
	background-color: #00c602;
	border-top: 1px solid #009703;
	border-bottom: 1px solid #009703;
	border-right: 1px solid #009703;
}
.bar-chart-bar.good {
	background-color: #fff700;
	border-top: 1px solid #d6cf00;
	border-bottom: 1px solid #d6cf00;
	border-right: 1px solid #d6cf00;
}
.bar-chart-bar.average {
	background-color: #ffd200;
	border-top: 1px solid #cea800;
	border-bottom: 1px solid #cea800;
	border-right: 1px solid #cea800;
}

.bar-chart-bars-cont {
	display: flex;
	flex-direction: column;
	gap: 20px;
	border-bottom: 1px solid #ccc;
}

.bar-chart-bar-label {
	position: absolute;
	height: 40px;
	line-height: 80px;
	text-indent: 10px;
	z-index: 60;
	color: #000;
	font-size: large;
	font-weight: 700;
}

.bar-chart-tick-cont {
	position: absolute;
	height: 100%;
	width: 100%;
	z-index: 0;
}
.bar-chart-tick-numbers-cont {
	display: grid;
	height: 20px;
}
.bar-chart-tick-numbers-cont>:last-child,
.bar-chart-tick-cont>:last-child{
	background-color: #00000000;
}
.bar-chart-tick-cont>:first-child {
	background-color: #00000000;
}
.bar-chart-tick-numbers-cont {
	margin-top: 10px;
	margin-bottom: 10px;
	text-indent: 5px;
}
.chart-6 {
	display: grid;
	grid-template-columns: repeat(6, 20%);
}
.chart-5 {
	display: grid;
	grid-template-columns: repeat(5, 25%);
}
.bar-chart-tick {
	background-color: #ccc;
	height: calc(100% - 30px);
	width: 1px;
}
.bar-chart-tick-number {
	text-align: left;
	font-style: italic;
	font-weight: 600;
	color: #3d3d3d
}

@keyframes fadein {
	from { width: 0}
}