body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#invert-button>img {
	filter: invert();
}

/*Warnings*/
.warning {
	height: 100%;
	display: flex;
	align-items: center;
	gap: 5px;
}
.warning>img {
	height: 15px;
	filter:invert(30%) sepia(41%) saturate(7477%) hue-rotate(337deg) brightness(99%) contrast(90%);
}
.warning>.warning-text {
	font-style: italic;
	font-weight: 600;
	color: #f0314b;
}

/*Cards*/
.minimize-button {
	position: absolute;
    line-height: 5px;
    padding: 17.5px;
    right: 43.5px;
    background-color: #062a49;
    color: var(--card-base-light);
    font-weight: 500;
}
.minimize-button:hover {
	background-color: #041c31;
}

/*Float Container*/
.float-container.minimized {
	background-color: transparent;
	transition: 0.75s;
	pointer-events: none;
}
.float-container.minimized>.card {
	transition: 0.75s;
	min-height: 40px !important;
	max-height: 40px !important;
	overflow: hidden;
	pointer-events:all;
}
.float-container.minimized>.card>.card-content {
	display: none;
}

/*Settings*/
.settings-card {
	width: 60vw;
	min-height: 70vh;
	max-height: 70vh;
}
.settings-card>.card-content {
	padding: 0px;
}
.settings-card>.card-content>.side-bar-menu-cont {
	position: relative;
	height: calc(100% - 40px);
	background-color: transparent;
	display: flex;
	flex-direction: column;
	gap: 10px;
}
.settings-card>.card-content>.side-bar-menu-cont>.side-bar {
	height: calc(100% - 40px) !important;
	background-color: transparent;
}
.settings-card>.card-content>.side-bar-menu-cont>.side-bar-menu-controls>.action-button>img,
.settings-card>.card-content>.side-bar-menu-cont>.side-bar>.menu-button>img {
	filter: brightness(0) saturate(100%) invert(13%) sepia(0%) saturate(1%) hue-rotate(207deg) brightness(97%) contrast(80%);
}
.settings-card>.card-content>.side-bar-menu-cont>.side-bar {
	display: flex;
	flex-direction: column;
	row-gap: 10px;
}
.settings-card>.card-content>.side-bar-menu-cont>.side-bar>.menu-button {
	color: #212121;
	border-radius: 5px;
}
.settings-card>.card-content>.side-bar-menu-cont>.side-bar-menu-controls>.action-button:hover,
.settings-card>.card-content>.side-bar-menu-cont>.side-bar>.menu-button:hover,
.settings-card>.card-content>.side-bar-menu-cont>.side-bar>.menu-button.selected {
	background-color: #ccc;
}
.settings-card>.card-content>.side-bar-container#collapsed {
	top: 40px;
	width: calc(100% - 55px);
	height: calc(100% - 40px);
	left: 55px;
}
.settings-card>.card-content>.side-bar-container {
	top: 40px;
	width: calc(100% - 180px);
	height: calc(100% - 40px);
	left: 180px;
}
.settings-card>.card-content>.side-bar-menu-cont>.action-row#side-bar-bottom-buttons>.action-button>img {
	
}
.settings-card>.card-content>.side-bar-menu-cont>.action-row#side-bar-bottom-buttons>.action-button:hover {
	background-color: #ccc;
}
.settings-card>.card-content>.side-bar-menu-cont>.action-row#side-bar-bottom-buttons>.action-button {
	color: #000;
}

/*Base classes*/

.outline-card {
	border: 1px solid #e2e2e2;
	filter: unset;
}

.no-padding {
	padding: 0;
}

.multi-form-item {
	display: flex;
	gap: 5px;
}

.two-thirds-top-column {
	display: grid;
	grid-template-rows: 66% 32%;
	gap: 2%;
	max-height: 100%;
	min-height: 100%;
}
.four-fifths-top-column {
	display: grid;
	grid-template-rows: 80% 18%;
	gap: 2%;
	max-height: 100%;
	min-height: 100%;
}

.one-fifth-top-column {
	display: grid;
	grid-template-rows: 18% 80%;
	gap: 2%;
	max-height: 100%;
	min-height: 100%;
}


#membership-options-card {
	max-height: 100%;
	min-height: 100%;
	font-size: smaller !important;
}

/*Creator Form Base Class*/
.creator-form {
	display: flex;
	flex-direction: column;
	gap: 10px;
}
.creator-form>.form-info-item {
	align-content: center;
	display: grid;
	grid-template-columns: 40% 60%;
}
.creator-form>.form-info-item>.form-info-title {
	font-size: x-large;
	height: 100%;
}
.creator-form>.form-info-item>input {
	padding: 10px;
	width: 100%;
}
.creator-form>.checkbox-form {
	display: flex;
	flex-direction: column;
	padding: 0px;
}
.creator-form>.checkbox-form>.form-info-item {
	width: 100%;
	row-gap: 10px;
	padding: 10px;
	display: grid;
	grid-template-columns: 90% 10%;
}
.creator-form>.checkbox-form>.form-info-item>.form-info-title {
	font-size: 20px;
}

/*SERVICE ITEMS*/
/*Repair Table*/
.si-repairs>.datatable>.tablerow {
	display: grid;
	grid-template-columns: 10% 20% 30% 20% 20%;
}
.si-remove-repair-button:hover {
	background-color: #ccc;
}
.si-remove-repair-button {
	background-color: transparent;
	width: 35px;
	height: 35px;
	filter: unset;
}
.si-remove-repair-button>img {
	margin-top: 7.5px;
	height: 20px;
	width: 20px;
}

.repair-table {
	min-width: 100%;
	max-width: 100%;
}

.repair-table>.card-content {
	padding: 0px;
}

#title-row {
	padding: 5px;
}
#title-row>input {
	padding: 8px;
}
#title-row>.action-button {
	height: 35px;
	width: 35px;
	font-size: 30px;
	background-color: #0b4e8a;
}
#title-row>.action-button:hover {
	background-color: #07355e;
}

.si-card, .repair-table {
	filter: unset;
	border: 1px solid #ccc;
}

.repair-table {
	overflow-y: overlay;
}

.si-table {
	border-bottom: 1px solid #e2e2e2;
	display: grid;
	grid-template-columns: 10% 13% 54% 14.5% 5%;
	padding: 0px;
	align-items: center;
	height: fit-content;
	height: fit-content;
}
.si-table.other-repair {
	grid-template-columns: 10% 13% 54% 9.25% 5.25% 5%;
}
.si-tablerow-price::before {
	content: '$'
}
.si-table:hover {
	background-color: #e2e2e2;
}

#add-item {
	width: max-content;
	padding-left: 10px;
	padding-right: 10px;
}

.si-more-info {
	display: grid;
	grid-template-columns: 95% 5%;
}

.si-tag {
	font-size: 20px;
	font-weight: 550;
	font-style: italic;
	color: #212121;
}
.si-id {
	font-size: 17.5px;
	color: #646464;
}

.repair-checkboxes {
	border: 1px solid #ccc;
	padding: 10px;
	border-radius: 10px;
	display: flex;
	flex-direction: column;
	row-gap: 10px
}
.repair-checkboxes>.form-info-item {
	display: grid;
	grid-template-columns: 80% 15%;
}
.repair-checkboxes>.form-info-item>.form-info-title {
	text-align: left;
	width: 100%;
}

.repair-checkboxes.repair-table-checks {
	display: flex;
	flex-direction: row;
	width: 100%;
	border: 0px;
}
.repair-checkboxes.repair-table-checks>.form-info-item {
	width: 100%;
}
.repair-checkboxes.repair-table-checks>.form-info-item>.form-info-title {
	font-weight: 500 !important;
}
.more-info-button,.Text-Only-Button {
	width: max-content;
	background-color: transparent;
	filter: unset;
	color: #c94a4c;
	cursor: pointer;
	font-weight: 650;
}

.more-info-button:hover,.Text-Only-Button:hover {
	background-color: transparent;
	color: #912d2f;
}

/*SERVICE ITEMS DETAILS CARD*/
#service-items-detail-card {
	min-width: 70vw;
	max-width: 70vw;
	height: 80vh;
}
#service-items-detail-card-card-content {
	display: grid;
	grid-template-columns: 50% 50%;
	overflow-y: overlay;
	height: calc(80vh - 40px);
}
.service-items-detail-form {
	display: flex;
	flex-direction: column;
	row-gap: 10px;
	font-size: 20px;
}
.service-items-detail-form>.form-info-item {
	display: grid;
	grid-template-columns: 50% 50%;
	align-items: center;
}
.service-items-detail-form>.form-info-item>.form-info-title {
	font-size: 21px;
}
.service-items-detail-form>.form-info-item>input {
	width: 100%;
	padding: 10px;
}

/*CONTRACT CARD*/
#contract-card {
	width: 20vw;
}
#contract-card-card-content {
	display: flex;
	flex-direction: column;
	row-gap: 10px;
	padding-left: 20px;
	padding-right: 20px;
	height: calc(100% - 40px);
}
#contract-card-card-content>.form-info-item {
	display: grid;
	grid-template-columns: 40% 57.5%;
	gap: 2.5%;
}
#contract-card-card-content>.form-info-item>input {
	padding: 5px;
	width: 100%;
}
#contract-close-button {
	align-self: flex-end;
	margin-top: 10px;
	width: 40%;
}

/*WO INFORMATION*/
#wo-info-card {
	height: calc(100% - 20px);
	min-width: calc(100% - 20px);
	max-width: calc(100% - 20px);
	font-size: larger;
}
.wo-info-customer-info {
	width: 50%;
	font-size: larger;
	color: #212121;
	text-align: left;
}

#cust-info-customername {
	text-align: left;
}

#cust-info-contactphone {
	font-style: italic;
}

#cust-info-custcode>div {
	font-size: 55px;
}
#wo-descr-box {
	min-width: 100%;
	max-width: 100%;
	min-height: 200px;
	max-height: 200px;
	font-size: 20px;
}

/*Repair Page*/
#add-new-serviceitem, #reset-si-filters {
	background-color: #0b4e8a;
	font-size: 20px;
}
#add-new-serviceitem:hover, #reset-si-filters:hover {
	background-color: #07355e;
}
#reset-si-filters {
	width: fit-content;
	padding-left: 10px;
	padding-right: 10px;
}
.sort-button {
	background-color: transparent;
	width: fit-content;
	padding: 5px;
	filter: unset;
	height: 35px;
}
.sort-button:hover {
	background-color: #ccc;
}
.sort-button>img {
	padding: 0;
	height: 25px;
	width: 25px;
	filter: brightness(0) saturate(100%) invert(3%) sepia(7%) saturate(29%) hue-rotate(314deg) brightness(99%) contrast(79%);
}
.sort-button>img:hover {
	background-color: #ccc;
	filter: brightness(0) saturate(100%) invert(3%) sepia(7%) saturate(29%) hue-rotate(314deg) brightness(99%) contrast(79%);
}
.sort-button.selected {
	background-color: #ccc;
}
.sort-button.selected>img {
	filter: brightness(0) saturate(100%) invert(3%) sepia(7%) saturate(29%) hue-rotate(314deg) brightness(99%) contrast(79%);
}

#repairs-top-actions-cont {
	align-items: center;
}

.sort-drop-cont {
	width: 200px;
	align-items: center;
}
.sort-drop-cont>.form-info-title {
	font-size: 20px;
	text-align: center;
	color: #424242;
	font-weight: bolder;
	width: 100%;
}

/*Service items View settings*/
.service-items-list {
	padding-top: 10px;
	height: calc(100vh - 60px);
	width: 100%;
	overflow-y: scroll;
	padding-bottom: 10px;
}
.service-items-list#list {
	display: flex;
	flex-direction: column;
	row-gap: 2.5%
}
.service-items-list#medium-cards {
	display: grid !important;
	grid-template-columns: 50% 50% !important;
	column-gap: 0;
	row-gap: 20px;
}
.si-card.list {
	min-width: 97.5%;
	max-width: 97.5%;
	min-height: 88.5vh !important;
	max-height: 88.5vh !important;
}
.si-card.medium-cards {
	min-width: 97.5%;
	max-width: 97.5%;
	min-height: 88vh !important;
	max-height: 88vh !important;
}
.si-card.list>.card-content>.si-info,
.si-card.medium-cards>.card-content>.si-info {
	height: 24vh;
}
.si-card.list>.card-content>.si-repairs,
.si-card.medium-card>.card-content>.si-repairs {
	min-height:46vh;
	max-height: 46vh;
	display: flex;
	flex-direction: column;
}
.si-card.list>.card-content>.si-repairs>.repair-table,
.si-card.medium-cards>.card-content>.si-repairs>.repair-table {
	min-height: 54vh !important;
	max-height: 54vh !important;
}

/*Service Item Creator*/
#sitem-creator-card {
	min-height: fit-content;
	max-height: fit-content;
}
#sitem-select-form-item-cont {
	grid-template-columns: 70% 30%;
	gap: 0px;
}
#sitem-select-form-item-cont>.checkbox-cont {
	justify-self: end;
}
#sitem-creator-add-button {
	background-color: #0b4e8a;
}
#sitem-creator-add-button:hover {
	background-color: #07355e;
}

/*PRESENTATION*/
.score-card-container {
	width: 99.5%;
	display: grid;
	grid-template-columns: 56% 43%;
	height: calc(100vh - 80px);
	gap: 1%;
}
.score-card-container#system-score {
	display: grid;
	grid-template-columns: 43% 56%;
}

.summary-title-header {
	font-size: 40px;
    text-align: center;
    padding-top: 5px;
    padding-bottom: 5px;
    margin-bottom: 10px;
    background-color: rgb(15 70 119);
    color: #fff;
    width: 100%;
    border-radius: 10px;
}

.summary-header>.action-button {
	height: 45px;
	font-size: 30px;
	line-height: 40px;
	width: 100px;
	margin-right: 20px;
	background-color: rgb(15 70 119);
}
.summary-header>.action-button:hover {
	background-color: rgb(10, 54, 92);
}

.pres-left-full {
	min-height: 100% !important;
	max-height: 100% !important;
}

/*PRESENTATION: SCORING*/
.score-text-cont {
	padding: 10px;
	padding-top: 0;
}

.score-text,
.score-grade {
	text-align: left;
	font-size: large;
	font-weight: 600;
	padding-top: 5px;
	padding-bottom: 5px;
}

.grade-descr {
	text-align: left;
	font-size: x-large;
}

.grade-descr>span {
	font-weight: 600;
}
/*@todo adjust so it pulls color based on grade - key code them with numbers?*/
.score-actual {
	background-color: #fff700;
	padding: 5px;
	border-radius: 5px;
	border: 1px solid #d6cf00;
}

.guage-cont {
	display: flex;
	gap: 20px;
}

#guage-chart {
	justify-self: start;
	width: 130px;
	height: 100px;
}

/*Checklist Scoring*/
#ChecklistSummaryCard-card-content {
	height: calc(100% - 40px);
	overflow-y: scroll;
}
.grade-title-cont {
	display: grid;
	grid-template-columns: 80% 20%;
	font-size: 20px;
	justify-items: left;
	margin-top: 10px;
	margin-bottom: 10px;
}
.grade-score {
	justify-self: end;
	padding: 10px;
	border-radius: 10px;
	font-weight: 600;
}
.grade-score::after {
	content: '%';
}
.grade-title {
	padding: 10px;
	border-radius: 10px;
	font-weight: 600;
}
.grade-title.Excellent,
.grade-score.Excellent {
	background-color: rgb(0, 255, 98);
    border: 1px solid rgb(0, 165, 63);
}
.grade-title.Good,
.grade-score.Good {
	background-color: rgb(255, 187, 0);
    border: 1px solid rgb(165, 121, 0);
}
.grade-title.Average,
.grade-score.Average {
	background-color: rgb(255, 247, 0);
    border: 1px solid rgb(178, 172, 0);
}
/*Checklist summary header*/
.checklist-summary-list>.tablerow.header {
	display: none;
}
.checklist-summary-list>.data-table {
	height: max-content;
	border: 1px solid #ccc;
	background-color: transparent;
	border-radius: 10px;
	padding: 0px;
	display: grid;
	grid-template-columns: repeat(2, 50%);
	font-size: larger;
	align-content: center;
    justify-content: center;
	overflow: hidden;
}
.checklist-summary-list>.data-table>.tablerow {
	/*border-bottom: 1px solid #ccc;
	border-right: 1px solid #ccc;
	grid-template-columns: 40% 40% 20%;
	padding: 0px !important;
	width: 100%;
	background-color: transparent;
	height: fit-content;*/
	display: grid;
    grid-template-columns: 37.5% 42.5% 12.5%;
    gap: 0.5%;
    padding: 5px;
    border-top: 1px solid #ccc;
    background-color: transparent;
    align-items: center;
    justify-content: center;
	height: 100%;
}
.checklist-summary-list>.data-table>.tablerow>div {
	background-color: transparent !important;
	padding: 10px;
	margin: 0px !important;
}
.checklist-summary-list>.data-table>:first-child,
.checklist-summary-list>.data-table>:nth-child(2) {
	border-top: 0px solid #ccc;
}
.checklist-summary-list>.data-table>:nth-child(2n + 1) {
	border-right: 1px solid #ccc;
}
.checklist-summary-list>.data-table>:last-child {
	border-bottom: 0px;
}

/*Popout Repair Table*/
.repair-table-popout-card {
	min-width: 90vw;
	min-height: 70vh;
	max-height: 70vh;
	position: absolute;
	bottom: 10px;
	margin-left: 5vw;
}
#repair-table-repair-cont {
	height: 31.5vw;
	padding: 0;
}
#repair-table-repair-cont>.data-table {
	height: 90.5%;
}
#repair-table-repair-cont>.tablerow.header {
	grid-template-columns: 10% 10% 60% 10% 10%;
	font-weight: 550;
	border-bottom: 1px solid #ccc;
	font-size: 20px;
	padding: 0px;
	margin: 0px;
}
#repair-table-repair-cont>.tablerow.header>div {
	background-color: transparent;
}
#repair-table-repair-cont>.data-table>.tablerow {
	height: 30px;
	cursor: pointer;
	border-bottom: 1px solid #ccc;
	display: grid;
	padding: 5px;
	align-items: center;
	margin: 0px;
	grid-template-columns: 10% 10% 60% 10% 10%;
	font-size: 17.5px;
}
#repair-table-repair-cont>.data-table>.tablerow>div {
	margin: 0px;
}
#repair-table-repair-cont>.data-table>.tablerow:hover {
	background-color: #ccc;
}



/*CHECKLIST MOCK*/
/*Container*/
.checklists-list {
	padding-top: 10px;
	height: calc(100vh - 117px);
	width: 100%;
	overflow-y: overlay;
	padding-bottom: 10px;
}
.checklists-list#list {
	display: flex;
	flex-direction: column;
	row-gap: 2.5%;
}
.checklists-list#medium-cards {
	display: grid !important;
	grid-template-columns: 49.5% 49.5% !important;
	column-gap: 0;
	row-gap: 10px;
	gap: 1%;
	overflow-y: overlay !important;
}
/*Cards*/
.checklist-card {
	min-width: 100%;
	min-height: fit-content;
}
.checklists-list#medium-cards>.checklist-card {
	min-height: calc(100vh - 140px);
	max-height: calc(100vh - 140px);
}
.checklist-card>.card-content {
	height: calc(100% - 40px);
	overflow-y: overlay;
}
.checklist-card>.card-menubar {
	position: relative;
	background-color: #5c1d1d;
}
.checklist-card.cl-green>.card-menubar {
	background-color: #1d5c25;
}
.checklist-card.cl-purple>.card-menubar {
	background-color: #3c1a52;
}
.checklist-card.cl-orange>.card-menubar {
	background-color: #d37116;
}
.checklist-card.cl-blue>.card-menubar {
	background-color: #1662d3;
}
.checklist-card.cl-red>.card-menubar {
	background-color: #5c1d1d !important;
}

/*Filters*/
/*CHIP COLORS*/
.checklist-chip {
	background-color: #912d2f !important;
	border: 1px solid #5c1d1d !important;
	color: #fff !important;
}
.checklist-chip:hover {
	background-color: #5c1d1d !important;
}
.checklist-chip.selected {
	background-color: #5c1d1d !important;
}
/*GREEN*/
.checklist-chip.cl-green {
	background-color: #2d912d !important;
	border: 1px solid #1d5c25 !important;
	color: #fff !important;
}
.checklist-chip.cl-green:hover {
	background-color: #1d5c25 !important;
}
.checklist-chip.cl-green.selected {
	background-color: #1d5c25 !important;
}
/*PURPLE*/
.checklist-chip.cl-purple {
	background-color: #692d91 !important;
	border: 1px solid #3c1a52 !important;
	color: #fff !important;
}
.checklist-chip.cl-purple:hover {
	background-color: #3c1a52 !important;
}
.checklist-chip.cl-purple.selected {
	background-color: #3c1a52 !important;
}
/*ORANGE*/
.checklist-chip.cl-orange {
	background-color: #ff8d21 !important;
	border: 1px solid #d37116 !important;
	color: #41250b !important;
}
.checklist-chip.cl-orange:hover {
	background-color: #d37116 !important;
}
.checklist-chip.cl-orange.selected {
	background-color: #d37116 !important;
}
/*BLUE*/
.checklist-chip.cl-blue {
	background-color: #1662d3 !important;
	border: 1px solid #0c408f !important;
	color: #fff !important;
